<template>
	<div class="login">
		<div class="logo">
			<img src="../assets/logo.jpg" alt="my login image">
		</div>

		<!-- 手机号 -->
		<InputGroup
			type='number'
			v-model='phone'
			placeholder="手机号"
			:btnTitle='btnTitle'
			:disabled='disabled'
			:error='errors.phone'
			@btnClick='getVerifyCode'
	
		/>

		<!-- 验证码 -->
		<InputGroup
			type='number'
			v-model='verifyCode'
			placeholder="验证码"
			:error='errors.code'
		/>

		<!-- 用户服务协议 -->
		<div class="login_des">
			<p>
				新用户登录即自动注册,表示已同意
				<span>《用户服务协议》</span>
			</p>
		</div>

		<!-- 登录按钮 -->
		<div class="login_btn">
			<button :disabled='isClick' @click="handleLogin">登录</button>
		</div>
		<br>
		<h2>
			手机号：18303473318
			<br>
			验证码：783458
		</h2>
	</div>
</template>

<script>
	// @ is an alias to /src
	import InputGroup from '../components/InputGroup'
	import { ref, watch, reactive, toRefs, onBeforeUnmount,computed } from "@vue/composition-api";
	import axios from 'axios'
	export default {
		name: 'Login',
		components: {
			InputGroup
		},
		setup(props,{root}) {
			const data = reactive({
				phone:'',
				verifyCode:'',
				errors:{},
				btnTitle:'获取验证码',
				disabled:false
			})

			const getVerifyCode=()=>{
				if(validatePhone()){
					//发送网络请求
					axios.post('/api/posts/sms_send',{
						sid:'5e56fb5103455971fe210ba4d79b8c3e',
						token:'2f76785c2674c6ce75fa0f7e7d4038fc',
						appid:'3142e8acf9704008a89e6042fb04aa17',
						temlateid:'577782',
						phone:data.phone
					}).then(res=>{
						// console.log(res);
						validateBtn()
					})
					
				}
				
			}

			const validateBtn=()=>{
				let time=5;
				let timer=setInterval(()=>{
					if(time==0){
						clearInterval(timer);
						data.btnTitle='获取验证码';
						data.disabled=false;
					}else{
						//倒计时
						data.btnTitle=time+'秒后重试';
						data.disabled=true;
						time--;
					}
				},1000);
			}

			const validatePhone=()=>{
				if(!data.phone){
					data.errors={
						phone:'手机号码不能为空'
					}
					return false
				}else if(!/^1[345678]\d{9}$/.test(data.phone)){
					data.errors={
						phone:'请填写正确的手机号码'
					}
					return false
				}else{
					data.errors={};
					return true;
				}
			}

			const isClick=computed(()=>{
				if(!data.phone || !data.verifyCode){
					return true
				}else{
					return false
				}				 			
			})

			const handleLogin=()=>{
				//取消错误提醒
				data.errors={}
				//发送请求
				axios.post('/api/posts/sms_back',{
					phone:data.phone,
					code:data.verifyCode
				}).then(res=>{
					// console.log(res.data);
					//检验成功 设置登录状态并且跳转到
					localStorage.setItem('ele_login',res.data.user._id);
					root.$router.push({
						path:'/'
					})
				}).catch(err=>{
					//返回错误信息
					data.errors={
						code:err.response.data.msg
					}
				})
			}
			return {
				...toRefs(data),
				getVerifyCode,
				isClick,
				handleLogin
			}
		}

	}
</script>

